___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --magenta-escuro: #8b008b;
    --neve: #fffafa;
}

* {
    box-sizing: border-box;
    outline: 0;
}

body {
    margin: 0;
    padding: 0;
    background: var(--magenta-escuro);
    font-family: sans-serif;
    font-size: 1em;
    line-height: 1.5em;
}

.section {
    max-width: 560px;
    margin: 50px auto;
    background-color: var(--neve);
    border-radius: 20px;
    padding: 20px;
}

.section .display {
    display: block;
    width: 100%;
    font-size: 20px;
    height: 40px;
    padding: 0 15px;
    margin-bottom: 20px;
}

.section .form input[type="checkbox"] {
    width: 20px;
    height: 20px;
}

.section .form .generate {
    display: block;
    width: 100%;
    border: none;
    background: var(--magenta-escuro);
    color: var(--neve);
    font-size: 20px;
    font-weight: bold;
    height: 40px;
    cursor: pointer;
    margin-top: 20px;
}

.section .form .generate:hover {
    opacity: 80%;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,SAAS;IACT,UAAU;IACV,iCAAiC;IACjC,uBAAuB;IACvB,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,6BAA6B;IAC7B,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,cAAc;IACd,WAAW;IACX,eAAe;IACf,YAAY;IACZ,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,WAAW;IACX,YAAY;IACZ,iCAAiC;IACjC,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,YAAY;AAChB","sourcesContent":[":root {\n    --magenta-escuro: #8b008b;\n    --neve: #fffafa;\n}\n\n* {\n    box-sizing: border-box;\n    outline: 0;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    background: var(--magenta-escuro);\n    font-family: sans-serif;\n    font-size: 1em;\n    line-height: 1.5em;\n}\n\n.section {\n    max-width: 560px;\n    margin: 50px auto;\n    background-color: var(--neve);\n    border-radius: 20px;\n    padding: 20px;\n}\n\n.section .display {\n    display: block;\n    width: 100%;\n    font-size: 20px;\n    height: 40px;\n    padding: 0 15px;\n    margin-bottom: 20px;\n}\n\n.section .form input[type=\"checkbox\"] {\n    width: 20px;\n    height: 20px;\n}\n\n.section .form .generate {\n    display: block;\n    width: 100%;\n    border: none;\n    background: var(--magenta-escuro);\n    color: var(--neve);\n    font-size: 20px;\n    font-weight: bold;\n    height: 40px;\n    cursor: pointer;\n    margin-top: 20px;\n}\n\n.section .form .generate:hover {\n    opacity: 80%;\n}"],"sourceRoot":""}]);
